import type { ActionArgs } from "@remix-run/node";
import { json, redirect } from "@remix-run/node";
import { Form, Link, useActionData } from "@remix-run/react";
import { withZod } from "@remix-validated-form/with-zod";
import { AiOutlineClose } from "react-icons/ai";
import { BiCross, BiPlus, BiTransfer } from "react-icons/bi";
import { ValidatedForm, validationError } from "remix-validated-form";
import { z } from "zod";
import InputField from "~/components/form/InputField";
import SelectField from "~/components/form/SelectField";
import SubmitButton from "~/components/form/SubmitButton";
import Modal from "~/components/Modal";
import ChangeExpense from "~/components/pages/expenses/ChangeExpense";
import { createExpense } from "~/models/expense.server";
import { EXPENSE_CATEGORIES } from "~/utils/constants";

export const validator = withZod(
  z
    .object({
      title: z.string().min(1, { message: "Title is required" }),
      description: z.string().min(1, { message: "Description is required" }),
      amount: z.coerce.number().min(1, { message: "Amount is required" }),
      pricePerUnit: z.coerce
        .number()
        .min(1, { message: "Price per unit is required" }),
      groupId: z.string().min(1, { message: "Group is required" }),
      ocurrence: z.enum(["once", "daily", "weekly", "monthly", "yearly"]),
      payDate: z.coerce.date(),
      category: z.enum(Object.keys(EXPENSE_CATEGORIES)),
      amountPaid: z.coerce
        .number()
        .min(0, {
          message: "Amount paid must be greater than 0",
        })
        .optional(),
    })
    .refine((data) => data.amountPaid <= data.amount * data.pricePerUnit, {
      message:
        "Amount paid must be less than or equal to the amount * price per unit",
      path: ["amountPaid"],
    })
);

export async function action({ request }: ActionArgs) {
  const formData = await request.formData();
  const result = await validator.validate(formData);

  if (result.error) {
    return validationError(result.error);
  }

  const expense = await createExpense(request, result.data);

  if (!expense) {
    return json({
      error: "An error occurred while creating the expense",
    });
  }
  return redirect(`/app/expenses`);
}

export default function NewExpensePage() {
  return <ChangeExpense type="new" />;
}

