import type { Group } from "@prisma/client";
import { redirect } from "@remix-run/server-runtime";
import invariant from "tiny-invariant";

import { prisma } from "~/db.server";
import { authenticator } from "~/utils/auth.server";

export const deleteGroup = async (request: Request, id: Group["id"]) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");
  const group = await prisma.group.delete({
    where: {
      id: id,
    },
  });

  return group;
};

export const getGroup = async (request: Request, id: Group["id"]) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");
  const group = await prisma.group.findUnique({
    where: {
      id: id,
    },
    include: {
      expenses: true,
      investments: true,
      comments: true,
    },
  });

  invariant(group, "Group does not exist");

  const groupUsers = await prisma.groupUsers.findMany({
    where: {
      groupId: id,
    },
  });

  const usersData = await Promise.all(
    groupUsers.map(async (user) => {
      const userData = await prisma.user.findUnique({
        where: {
          id: user.userId,
        },
      });
      return userData;
    })
  );

  return {
    ...group,
    users: usersData,
  };
};

export const createGroup = async (
  request: Request,
  group: Group,
  emails: string[]
) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");
  const email = user.email;
  const newGroup = await prisma.group.create({
    data: {
      title: group.title,
      description: group.description,
    },
  });

  // add user to group
  await prisma.groupUsers.create({
    data: {
      userId: user.id,
      groupId: newGroup.id,
      isAdmin: true,
    },
  });
  for (const email of emails) {
    const userToAdd = await prisma.user.findUnique({
      where: {
        email: email,
      },
    });
    if (userToAdd) {
      await prisma.groupUsers.create({
        data: {
          userId: userToAdd.id,
          groupId: newGroup.id,
        },
      });
    }
  }
  return newGroup;
};

