export const getGroups = async (request: Request) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");
  const id = user.id;
  const userGroups = await prisma.groupUsers.findMany({
    where: {
      userId: id,
    },
  });
  const groups = await Promise.all(
    userGroups.map(async (group) => {
      const data = await prisma.group.findUnique({
        where: {
          id: group.groupId,
        },
        include: {
          expenses: true,
          investments: true,
        },
      });
      const users = await prisma.groupUsers.findMany({
        where: {
          groupId: group.groupId,
        },
      });
      return {
        ...data,
        users: users.map((user) => user.userId),
      };
    })
  );
  return groups;
};

export const updateGroup = async (
  request: Request,
  id: Group["id"],
  group: Group,
  newEmails: string[]
) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");
  const updatedGroup = await prisma.group.update({
    where: {
      id: id,
    },
    data: {
      title: group.title,
      description: group.description,
    },
  });

  const groupUsers = await prisma.groupUsers.findMany({
    where: {
      groupId: id,
    },
  });

  const users = groupUsers.map((user) => user.userId);

  for (const email of newEmails) {
    const userToAdd = await prisma.user.findUnique({
      where: {
        email: email,
      },
    });
    if (userToAdd) {
      if (!users.includes(userToAdd.id)) {
        await prisma.groupUsers.create({
          data: {
            userId: userToAdd.id,
            groupId: id,
          },
        });
      }
    }
  }

  return updatedGroup;
};
