import type { ActionArgs, LoaderArgs } from "@remix-run/node";
import { json, redirect } from "@remix-run/node";
import { Form, Link, useActionData, useCatch, useLoaderData } from "@remix-run/react";
import { AiOutlineClose } from "react-icons/ai";
import { BiTransfer } from "react-icons/bi";
import { ValidatedForm, validationError } from "remix-validated-form";
import invariant from "tiny-invariant";
import InputField from "~/components/form/InputField";
import SelectField from "~/components/form/SelectField";
import SubmitButton from "~/components/form/SubmitButton";
import Modal from "~/components/Modal";
import ChangeExpense from "~/components/pages/expenses/ChangeExpense";
import { deleteExpense, getExpense, updateExpense } from "~/models/expense.server";
import { EXPENSE_CATEGORIES } from "~/utils/constants";
import { validator } from "./new";

export async function loader({ request, params }: LoaderArgs) {
  invariant(params.expenseId, "expenseId not found");

  const expense = await getExpense(request, params.expenseId);
  return json({
    expense
  });
}

export async function action({ request, params }: ActionArgs) {
  invariant(params.expenseId, "expenseId not found");
  const formData = await request.formData();
  const action = formData.get("action")
  if (action === "delete") {
    const expense = await deleteExpense(request, params.expenseId);
    if (!expense) {
      return json(
        {
          error: "An error occurred while deleting the expense"
        }
      )
    }
    return redirect(`/app/expenses`);
  }
  const result = await validator.validate(
    formData
  );

  if (result.error) {
    return validationError(result.error);
  }

  const expense = await updateExpense(
    request,
    {
      ...result.data,
      id: params.expenseId
    }
  )

  if (!expense) {
    return json(
      {
        error: "An error occurred while updating the expense"
      }
    )
  }
  return redirect(`/app/expenses`);
}

export default function NoteDetailsPage() {
  return <ChangeExpense type="edit" />;
}


