import Table from "~/components/Table";
import { BiGroup } from "react-icons/bi"
import { Outlet, useLoaderData } from "@remix-run/react";
import { json, LoaderArgs } from "@remix-run/server-runtime";
import { getGroups } from "~/models/group.server";

export async function loader({ request }: LoaderArgs) {
    const groups = await getGroups(request)

    return json({
        groups
    })
}

export default function NoteIndexPage() {
    const data = useLoaderData<typeof loader>()
    return (
        <>
            <Outlet />
            <Table title="Groups"
                subtitle="All your groups"
                individualUrl="/app/groups/"
                createUrl="/app/groups/new"
                icon={
                    BiGroup
                }
                label="Expense"
                pluralLabel="Groups"
                data={data.groups}
                columns={[
                    {
                        accessorKey: "name",
                        cell: (row) => <span>
                            {row.row.original.title}
                        </span>
                    },
                    {
                        accessorKey: "investments",
                        cell: (row) => {
                            return <span>
                                {row.row.original.investments.length}
                            </span>
                        }
                    },
                    {
                        accessorKey: "expenses",
                        cell: (row) => <span>
                            {row.row.original.expenses.length}
                        </span>
                    },
                    {
                        accessorKey: "members",
                        cell: (row) => <span>
                            {row.row.original.users.length}
                        </span>
                    }
                ]}
            />
        </>
    );
}
