import { Link, useNavigate } from "@remix-run/react";
import { useEffect } from "react";
import { IconType } from "react-icons";

export default function Modal({ children, title,
    icon,
    description, onCloseUrl }: { children: React.ReactNode, title: string, onCloseUrl: string, description: string, icon: IconType }) {
    const navigate = useNavigate()
    useEffect(() => {
        const handleEscape = (event: KeyboardEvent) => {
            if (event.key === "Escape") {
                navigate(onCloseUrl)
            }
        }
        window.addEventListener("keydown", handleEscape)
        return () => window.removeEventListener("keydown", handleEscape)
    }, [])
    return (
        <div
            className="hs-overlay w-full h-full fixed top-0 left-0 z-[70] overflow-x-hidden overflow-y-auto"
        >
            <Link
                to={onCloseUrl} className="w-full h-full fixed top-0 left-0 bg-slate-100/70 dark:bg-slate-800/70 backdrop-blur-sm backdrop-filter" />
            <div className="animate-fade-in duration-500 ease-out transition-all sm:max-w-lg sm:w-full m-3 sm:mx-auto">
                <div className="relative flex flex-col bg-slate-100 shadow-lg rounded-xl dark:bg-slate-800">
                    <div className="relative overflow-hidden min-h-[8rem] bg-slate-900 text-center rounded-t-xl">
                        {/* Close Button */}
                        <div className="absolute top-2 right-2">
                            <Link
                                to={onCloseUrl}
                                className="inline-flex flex-shrink-0 justify-center items-center h-8 w-8 rounded-md text-slate-500 hover:text-slate-400 focus:outline-none focus:ring-2 focus:ring-slate-400 focus:ring-offset-2 focus:ring-offset-white transition-all text-sm dark:focus:ring-slate-700 dark:focus:ring-offset-slate-800"
                                data-hs-overlay="#hs-bg-slate-on-hover-cards"
                            >
                                <span className="sr-only">Close</span>
                                <svg
                                    className="w-3.5 h-3.5"
                                    width={8}
                                    height={8}
                                    viewBox="0 0 8 8"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                >
                                    <path
                                        d="M0.258206 1.00652C0.351976 0.912791 0.479126 0.860131 0.611706 0.860131C0.744296 0.860131 0.871447 0.912791 0.965207 1.00652L3.61171 3.65302L6.25822 1.00652C6.30432 0.958771 6.35952 0.920671 6.42052 0.894471C6.48152 0.868271 6.54712 0.854471 6.61352 0.853901C6.67992 0.853321 6.74572 0.865971 6.80722 0.891111C6.86862 0.916251 6.92442 0.953381 6.97142 1.00032C7.01832 1.04727 7.05552 1.1031 7.08062 1.16454C7.10572 1.22599 7.11842 1.29183 7.11782 1.35822C7.11722 1.42461 7.10342 1.49022 7.07722 1.55122C7.05102 1.61222 7.01292 1.6674 6.96522 1.71352L4.31871 4.36002L6.96522 7.00648C7.05632 7.10078 7.10672 7.22708 7.10552 7.35818C7.10442 7.48928 7.05182 7.61468 6.95912 7.70738C6.86642 7.80018 6.74102 7.85268 6.60992 7.85388C6.47882 7.85498 6.35252 7.80458 6.25822 7.71348L3.61171 5.06702L0.965207 7.71348C0.870907 7.80458 0.744606 7.85498 0.613506 7.85388C0.482406 7.85268 0.357007 7.80018 0.264297 7.70738C0.171597 7.61468 0.119017 7.48928 0.117877 7.35818C0.116737 7.22708 0.167126 7.10078 0.258206 7.00648L2.90471 4.36002L0.258206 1.71352C0.164476 1.61976 0.111816 1.4926 0.111816 1.36002C0.111816 1.22744 0.164476 1.10028 0.258206 1.00652Z"
                                        fill="currentColor"
                                    />
                                </svg>
                            </Link>
                        </div>
                        {/* End Close Button */}
                        {/* SVG Background Element */}
                        <figure className="absolute inset-x-0 bottom-0">
                            <svg
                                preserveAspectRatio="none"
                                xmlns="http://www.w3.org/2000/svg"
                                x="0px"
                                y="0px"
                                viewBox="0 0 1920 100.1"
                            >
                                <path
                                    fill="currentColor"
                                    className="fill-white dark:fill-slate-800"
                                    d="M0,0c0,0,934.4,93.4,1920,0v100.1H0L0,0z"
                                />
                            </svg>
                        </figure>
                        {/* End SVG Background Element */}
                    </div>
                    <div className="relative z-10 -mt-12">
                        {/* Icon */}
                        <span className="mx-auto flex justify-center items-center w-[62px] h-[62px] rounded-full border border-slate-200 bg-slate-100 text-slate-700 shadow-sm dark:bg-slate-800 dark:border-slate-700 dark:text-slate-400">
                            {
                                icon({
                                    className: 'w-6 h-6'
                                })
                            }
                        </span>
                        {/* End Icon */}
                    </div>
                    {/* Body */}
                    <div className="p-4 sm:p-7 overflow-y-auto">
                        <div className="text-center">
                            <h3 className="text-lg font-semibold text-slate-800 dark:text-slate-200">
                                {title}
                            </h3>
                            <p className="text-sm text-slate-500">
                                {description}
                            </p>
                        </div>
                        <div className="mt-5">
                            {children}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}