import type { Investment } from "@prisma/client";
import { redirect } from "@remix-run/server-runtime";

import { prisma } from "~/db.server";
import { authenticator } from "~/utils/auth.server";
import { getUserGroups } from "./user.server";

export const getInvestments = async (request: Request) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");
  const userGroups = await getUserGroups(user.id);
  const investments = await prisma.investment.findMany({
    where: {
      OR: [
        {
          groupId: {
            in: userGroups.map((group) => group.id),
          },
        },
        {
          userId: user.id,
        },
      ],
    },
  });

  return investments;
};

export const createInvestment = async (request: Request, data: Investment) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");

  const { groupId, ...otherData } = data;
  if (data.groupId === "individual") {
    const investment = await prisma.investment.create({
      data: {
        ...otherData,
        user: {
          connect: {
            id: user.id,
          },
        },
      },
    });
    return investment;
  } else {
    const investment = await prisma.investment.create({
      data: {
        ...otherData,
        Group: {
          connect: {
            id: data.groupId,
          },
        },
      },
    });
    return investment;
  }
};

export const updateInvestment = async (request: Request, data: Investment) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");

  const { groupId, ...otherData } = data;
  if (data.groupId === "individual") {
    const investment = await prisma.investment.update({
      where: {
        id: data.id,
      },
      data: {
        ...otherData,
        user: {
          connect: {
            id: user.id,
          },
        },
        Group: {
          disconnect: true,
        },
      },
    });

    return investment;
  } else {
    const investment = await prisma.investment.update({
      where: {
        id: data.id,
      },
      data: {
        ...otherData,
        Group: {
          connect: {
            id: data.groupId,
          },
        },
      },
    });

    return investment;
  }
};

export const getInvestment = async (request: Request, id: string) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");

  const investment = await prisma.investment.findUnique({
    where: {
      id,
    },
  });

  return investment;
};

export const deleteInvestment = async (request: Request, id: string) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");

  const investment = await prisma.investment.delete({
    where: {
      id,
    },
  });

  return investment;
};
