import { Authenticator } from "remix-auth";
import { FormStrategy } from "remix-auth-form";
import { createCookieSessionStorage, redirect } from "@remix-run/node";
import { getUserById, verifyLogin } from "~/models/user.server";

export let sessionStorage = createCookieSessionStorage({
  cookie: {
    name: "_session", // use any name you want here
    sameSite: "lax", // this helps with CSRF
    path: "/", // remember to add this so the cookie will work in all routes
    httpOnly: true, // for security reasons, make this cookie http only
    secrets: [process.env.SESSION_SECRET ?? "secretttokdoakdw"], // replace this with an actual secret
    secure: process.env.NODE_ENV === "production", // enable this in prod only
    expires: new Date(Date.now() + 60 * 60 * 24 * 1000), // 1 day
  },
});

export let authenticator = new Authenticator<{
  email: string;
  id: string;
}>(sessionStorage);

authenticator.use(
  new FormStrategy(async ({ form, context }: any) => {
    const email = form.get("email");
    const password = form.get("password");
    const user = await verifyLogin(email, password);
    if (user) {
      return { email: user.email, id: user.id };
    }
    throw new Error("Invalid email or password");
  })
);

export async function requireUserId(
  request: Request,
  redirectTo: string = new URL(request.url).pathname
) {
  const user = await authenticator.isAuthenticated(request);
  if (!user) {
    const searchParams = new URLSearchParams([["redirectTo", redirectTo]]);
    throw redirect(`/login?${searchParams}`);
  }
  return user;
}

export async function getUser(request: Request) {
  const user = await authenticator.isAuthenticated(request);
  if (!user) return redirect("/login");
  const userData = await getUserById(user.id);
  return { email: user.email, avatar: userData?.avatar, name: userData?.name };
}
