import * as AvatarPrimitive from '@radix-ui/react-avatar';
import clsx from 'clsx';

const Avatar = ({
    className = "",
    initials,
    imgSrc
}: {
    className?: string;
    initials: string;
    imgSrc: string;
}) => (
    <AvatarPrimitive.Root className={clsx("bg-slate-800 dark:bg-slate-100 inline-flex select-none items-center justify-center overflow-hidden align-middle h-[2.375rem] w-[2.375rem] rounded-full ring-2 ring-white dark:ring-slate-800", className)}>
        <AvatarPrimitive.Image
            className="h-full w-full rounded-[inherit] object-cover"
            src={imgSrc}
            alt="User avatar"
        />
        <AvatarPrimitive.Fallback
            className="text-slate-800 leading-1 flex h-full w-full items-center justify-center bg-slate-100 text-[15px] font-medium"
            delayMs={600}
        >
            {initials}
        </AvatarPrimitive.Fallback>
    </AvatarPrimitive.Root>
);

export default Avatar;