import { json, LoaderArgs } from "@remix-run/server-runtime";
import { BiTransfer } from "react-icons/bi";
import SummaryCard from "~/components/SummaryCard";
import { getExpenses } from "~/models/expense.server";
import { getGroups } from "~/models/group.server";
import { getInvestments } from "~/models/investment.server";
import { Responsive, WidthProvider } from "react-grid-layout";
import { Suspense, useState } from "react";
import reactGridLayoutCSS from "react-grid-layout/css/styles.css";
import reactResizableCSS from "react-resizable/css/styles.css";
import LineChart from "~/components/charts/LineChart.client";
import BarChart from "~/components/charts/BarChart.client";
import GroupChart from "~/components/charts/GroupChart.client";
import PieChart from "~/components/charts/PieChart.client";

export function links() {
  return [
    {
      rel: "stylesheet",
      href: reactResizableCSS,
    },
    {
      rel: "stylesheet",
      href: reactGridLayoutCSS,
    },
  ];
}

const ResponsiveGridLayout = WidthProvider(Responsive);

export const SUMMARY_CARDS = [
  {
    title: "Expenses",
    value: 1000,
    icon: BiTransfer,
    change: 10,
    changeType: "up",
    viewMoreUrl: "/app/expenses",
  },
  {
    title: "Investments",
    value: 1000,
    icon: BiTransfer,
    change: 10,
    changeType: "up",
    viewMoreUrl: "/app/investments",
  },
];

export async function loader({ request }: LoaderArgs) {
  const expenses = await getExpenses(request);
  const investments = await getInvestments(request);
  const groups = await getGroups(request);

  return json({
    expenses,
    investments,
    groups,
  });
}

export default function AppIndexRoute() {
  const [layouts, setLayouts] = useState({
    lg: [
      {
        i: "Investments",
        x: 0,
        y: 0,
        w: 2,
        h: 1,
      },
      {
        i: "Expenses",
        x: 2,
        y: 0,
        w: 2,
        h: 1,
      },
      {
        i: "Chart",
        x: 4,
        y: 0,
        w: 2,
        h: 1,
      },
      {
        i: "2Chart",
        x: 6,
        y: 0,
        w: 2,
        h: 1,
      },
    ],
  });
  return (
    <>
      <div className="mt-4">
        <ResponsiveGridLayout
          className="layout"
          layouts={layouts}
          /*onBreakpointChange={this.handleBreakPointChange}
          onLayoutChange={this.handleLayoutChange}*/
          isDraggable
          isResizable
          //draggableHandle=".grid-item__title"
          breakpoints={{ lg: 1280, md: 992, sm: 767, xs: 480, xxs: 0 }}
          cols={{ lg: 12, md: 10, sm: 6, xs: 4, xxs: 2 }}
        >
          {SUMMARY_CARDS.map((card) => (
            <div key={card.title} className="">
              <SummaryCard
                key={card.title}
                title={card.title}
                value={card.value}
                icon={card.icon}
                change={card.change}
                changeType={card.changeType}
                viewMoreUrl={card.viewMoreUrl}
              />
            </div>
          ))}
          <div key="Chart" className="">
            <Suspense fallback={<div>Loading...</div>}>
              <LineChart />
            </Suspense>
          </div>
          <div key="2Chart" className=""
          >
            <Suspense fallback={<div>Loading...</div>}>
              <GroupChart Chart={BarChart} />
            </Suspense>
          </div>
          <div key="3Chart" className=""
          >
            <Suspense fallback={<div>Loading...</div>}>
              <GroupChart Chart={PieChart} />
            </Suspense>
          </div>
        </ResponsiveGridLayout>
      </div>
    </>
  );
}
