import Table from "~/components/Table";
import { BiGroup, BiTransfer } from "react-icons/bi"
import { BsPerson } from "react-icons/bs"
import { Outlet, useLoaderData } from "@remix-run/react";
import { json, LoaderArgs } from "@remix-run/server-runtime";
import { getExpenses } from "~/models/expense.server";
import { EXPENSE_CATEGORIES } from "~/utils/constants";
import { getGroups } from "~/models/group.server";

export async function loader({ request }: LoaderArgs) {
    const expenses = await getExpenses(request)
    const groups = await getGroups(request)

    return json({
        expenses,
        groups
    })
}

export default function NoteIndexPage() {
    const data = useLoaderData<typeof loader>()
    return (
        <>
            <Outlet />
            <Table title="Expenses"
                subtitle="All your expenses"
                individualUrl="/app/expenses/"
                createUrl="/app/expenses/new"
                icon={
                    BiTransfer
                }
                label="Expense"
                pluralLabel="Expenses"
                data={data.expenses}
                columns={[
                    {
                        accessorKey: "name",
                        cell: (row) => <span>
                            {row.row.original.title}
                        </span>
                    },
                    {
                        accessorKey: "category",
                        cell: (row) => {
                            const category = EXPENSE_CATEGORIES[row.row.original.category]
                            return <span>
                                {`${category.logo} ${category.name}`}
                            </span>
                        }
                    },
                    {
                        accessorKey: "payDate",
                        header: "Due",
                        cell: (row) => <span>
                            {new Date(row.row.original.payDate).toLocaleDateString()}
                        </span>
                    },
                    {
                        accessorKey: "amount",
                    },
                    {
                        accessorKey: "price",
                        cell: (row) => <span>
                            {`$${row.row.original.pricePerUnit}`}
                        </span>
                    },
                    {
                        accessorKey: "paid",
                        cell: (row) => row.row.original.paid ?
                            <span className="inline-flex items-center gap-1.5 py-0.5 px-2 rounded-full text-xs font-medium bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200">
                                <svg className="w-2.5 h-2.5" xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" viewBox="0 0 16 16">
                                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z" />
                                </svg>
                                Paid
                            </span>
                            : <span className="inline-flex items-center gap-1.5 py-0.5 px-2 rounded-full text-xs font-medium bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200">
                                <svg className="w-2.5 h-2.5" xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" viewBox="0 0 16 16">
                                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM5.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293 5.354 4.646z" />
                                </svg>
                                Not paid
                            </span>
                    },
                    {
                        accessorKey: "group",
                        cell: (row) => row.row.original.groupId ?
                            <span className="inline-flex items-center gap-1.5 py-0.5 px-2 rounded-full text-xs font-medium bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200">
                                <BiGroup className="w-2.5 h-2.5" />
                                Group
                            </span>
                            : <span className="inline-flex items-center gap-1.5 py-0.5 px-2 rounded-full text-xs font-medium bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-200">
                                <BsPerson />
                                Individual
                            </span>

                    }
                ]}
            />
        </>
    );
}
