import type { ActionArgs, LoaderArgs } from "@remix-run/node";
import { json, redirect } from "@remix-run/node";
import {
  Form,
  Link,
  useActionData,
  useCatch,
  useLoaderData,
} from "@remix-run/react";
import { AiOutlineClose } from "react-icons/ai";
import { BiTransfer } from "react-icons/bi";
import { ValidatedForm, validationError } from "remix-validated-form";
import invariant from "tiny-invariant";
import InputField from "~/components/form/InputField";
import SelectField from "~/components/form/SelectField";
import SubmitButton from "~/components/form/SubmitButton";
import Modal from "~/components/Modal";
import ChangeExpense from "~/components/pages/expenses/ChangeExpense";
import ChangeInvestment from "~/components/pages/investments/ChangeInvestment";
import {
  deleteExpense,
  getExpense,
  updateExpense,
} from "~/models/expense.server";
import { getInvestment, updateInvestment } from "~/models/investment.server";
import { EXPENSE_CATEGORIES } from "~/utils/constants";
import { validator } from "./new";

export async function loader({ request, params }: LoaderArgs) {
  invariant(params.investmentId, "investmentId not found");

  const investment = await getInvestment(request, params.investmentId);
  return json({
    investment,
  });
}

export async function action({ request, params }: ActionArgs) {
  invariant(params.investmentId, "investmentId not found");
  const formData = await request.formData();
  const action = formData.get("action");
  if (action === "delete") {
    const investment = await deleteExpense(request, params.investmentId);
    if (!investment) {
      return json({
        error: "An error occurred while deleting the investment",
      });
    }
    return redirect(`/app/investments`);
  }
  const result = await validator.validate(formData);

  if (result.error) {
    return validationError(result.error);
  }

  const investment = await updateInvestment(request, {
    ...result.data,
    id: params.investmentId,
  });

  if (!investment) {
    return json({
      error: "An error occurred while updating the investment",
    });
  }
  return redirect(`/app/investments`);
}

export default function NoteDetailsPage() {
  return <ChangeInvestment type="edit" />;
}
