import Table from "~/components/Table";
import { BiGroup } from "react-icons/bi";
import { BsPerson } from "react-icons/bs";
import { Outlet, useLoaderData } from "@remix-run/react";
import { json, LoaderArgs } from "@remix-run/server-runtime";
import { INVESTMENT_CATEGORIES } from "~/utils/constants";
import { getGroups } from "~/models/group.server";
import { getInvestments } from "~/models/investment.server";
import { AiOutlineLineChart } from "react-icons/ai";

export async function loader({ request }: LoaderArgs) {
  const investments = await getInvestments(request);
  const groups = await getGroups(request);

  return json({
    investments,
    groups,
  });
}

export default function NoteIndexPage() {
  const data = useLoaderData<typeof loader>();
  return (
    <>
      <Outlet />
      <Table
        title="Investments"
        subtitle="All your investments"
        individualUrl="/app/investments/"
        createUrl="/app/investments/new"
        icon={AiOutlineLineChart}
        label="Investment"
        pluralLabel="Investments"
        data={data.investments}
        columns={[
          {
            accessorKey: "Symbol",
            cell: (row) => <span>{row.row.original.symbol}</span>,
          },
          {
            accessorKey: "category",
            cell: (row) => {
              const category = INVESTMENT_CATEGORIES[row.row.original.category];
              return <span>{`${category.logo} ${category.name}`}</span>;
            },
          },
          {
            accessorKey: "payDate",
            header: "Due",
            cell: (row) => (
              <span>
                {new Date(row.row.original.payDate).toLocaleDateString()}
              </span>
            ),
          },
          {
            accessorKey: "amount",
          },
          {
            accessorKey: "unitPrice",
            header: "Unit Price",
            cell: (row) => <span>{`$${row.row.original.unitPrice}`}</span>,
          },
          {
            accessorKey: "group",
            cell: (row) =>
              row.row.original.groupId ? (
                <span className="inline-flex items-center gap-1.5 rounded-full bg-blue-100 py-0.5 px-2 text-xs font-medium text-blue-800 dark:bg-blue-900 dark:text-blue-200">
                  <BiGroup className="h-2.5 w-2.5" />
                  Group
                </span>
              ) : (
                <span className="inline-flex items-center gap-1.5 rounded-full bg-gray-100 py-0.5 px-2 text-xs font-medium text-gray-800 dark:bg-gray-900 dark:text-gray-200">
                  <BsPerson />
                  Individual
                </span>
              ),
          },
        ]}
      />
    </>
  );
}
