import { Link, useActionData, useLoaderData, useMatches } from "@remix-run/react";
import { AiOutlineClose } from "react-icons/ai";
import { BiTransfer } from "react-icons/bi";
import { ValidatedForm } from "remix-validated-form";
import InputField from "~/components/form/InputField";
import SelectField from "~/components/form/SelectField";
import SubmitButton from "~/components/form/SubmitButton";
import Modal from "~/components/Modal";
import { validator } from "~/routes/app/expenses/new";
import { useMatchesData } from "~/utils";
import { EXPENSE_CATEGORIES } from "~/utils/constants";

export default function ChangeExpense({
    type = "edit"
}: {
    type?: "edit" | "new";
}) {
    const actionData = useActionData();
    const loaderData = useLoaderData()
    const match = useMatchesData("routes/app/expenses")

    return <Modal
        title={type === "edit" ? "Edit expense" : "New expense"}
        description={
            type === "edit"
                ? "You can edit the details of this expense here."
                : "You can create a new expense here."
        }
        onCloseUrl="/app/expenses"
        icon={
            BiTransfer
        }
    >
        <ValidatedForm method="post" className="space-y-5" validator={validator}>
            <div className="grid grid-cols-2 gap-2">
                <InputField
                    label="Title"
                    name="title"
                    defaultValue={loaderData?.expense?.title}
                />
                <SelectField
                    defaultValue={loaderData?.expense?.category}
                    label="Category"
                    name="category"
                    options={Object.keys(EXPENSE_CATEGORIES).map((key) => {
                        const label = EXPENSE_CATEGORIES[key as keyof typeof EXPENSE_CATEGORIES];
                        return {
                            label: `${label.logo} ${label.name}`,
                            value: key,
                        };
                    })}
                />
            </div>

            <InputField
                defaultValue={loaderData?.expense?.description}
                label="Description"
                name="description"
            />
            <div className="grid grid-cols-2 gap-2">
                <InputField
                    defaultValue={loaderData?.expense?.amount}
                    label="Amount"
                    name="amount"
                    type="number"
                />
                <InputField
                    defaultValue={loaderData?.expense?.pricePerUnit}
                    label="Price per unit"
                    name="pricePerUnit"
                    type="number"
                />
            </div>
            <div className="grid grid-cols-2 gap-2">
                <SelectField
                    defaultValue={loaderData?.expense?.ocurrence}
                    label="Ocurrence"
                    name="ocurrence"
                    options={[
                        { label: "Once", value: "once" },
                        { label: "Daily", value: "daily" },
                        { label: "Weekly", value: "weekly" },
                        { label: "Monthly", value: "monthly" },
                        { label: "Yearly", value: "yearly" },
                    ]}
                />
                <SelectField
                    label="Group"
                    name="groupId"
                    defaultValue={loaderData?.expense?.groupId ? loaderData?.expense?.groupId : "individual"}
                    options={[
                        {
                            label: "Individual",
                            value: "individual"
                        },
                        ...match.groups.map((group) => ({
                            label: `Group: ${group.title}`,
                            value: group.id
                        }))
                    ]}
                />
            </div>
            <div className="grid grid-cols-2 gap-2">
                <InputField
                    defaultValue={loaderData?.expense?.payDate ? new Date(loaderData?.expense?.payDate).toISOString().substr(0, 16) : new Date().toISOString().substr(0, 16)}
                    label="Pay date"
                    name="payDate"
                    type="datetime-local"
                />
                <InputField
                    defaultValue={loaderData?.expense?.amountPaid}
                    label="Amount paid"
                    name="amountPaid"
                    type="number"
                />
            </div>

            <div className="mt-5 flex justify-end gap-x-2">
                <Link
                    to="/app/expenses"
                    className="py-2 px-3 inline-flex justify-center items-center gap-2 rounded-md border font-medium bg-slate-100 text-slate-700 shadow-sm align-middle hover:bg-slate-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-white focus:ring-blue-600 transition-all text-sm dark:bg-slate-800 dark:hover:bg-slate-800 dark:border-slate-700 dark:text-slate-400 dark:hover:text-white dark:focus:ring-offset-slate-800"
                >
                    <AiOutlineClose className="w-4 h-4" />
                    Close
                </Link>
                {type === "edit" && (
                    <button type="submit" className="inline-flex items-center justify-center gap-2 rounded-md border border-transparent bg-red-500 py-3 px-4 text-sm font-semibold text-white transition-all hover:bg-red-600 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-offset-2 dark:focus:ring-offset-gray-800"
                        name="action"
                        value="delete"
                    >
                        Delete
                    </button>
                )}
                <SubmitButton />
            </div>
            {actionData?.error && (
                <div className="bg-red-50 border border-red-200 text-sm text-red-600 rounded-md p-4" role="alert">
                    <span className="font-bold">Error</span> {actionData?.error}
                </div>
            )}
        </ValidatedForm>
    </Modal>
}