import { useField } from "remix-validated-form";

type MyInputProps = {
    name: string;
    label: string;
    type?: string;
    defaultValue?: string;
    placeholder?: string;
};

const InputField = ({ name, label, type = "text", defaultValue = "",
    placeholder = ""
}: MyInputProps) => {
    const { error, getInputProps } = useField(name);
    return (
        <div className="relative">
            <label className="mb-2 block text-sm dark:text-white"
                htmlFor={name}>{label}</label>
            <input {...getInputProps({
                id: name,
            })} placeholder={placeholder}
                defaultValue={defaultValue} type={type} className="py-3 px-4 block w-full border-gray-200 rounded-md text-sm focus:border-blue-500 focus:ring-blue-500 dark:bg-slate-900 dark:border-gray-700 dark:text-gray-400"
            />
            {error && <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center pr-3">
                <svg
                    className="h-5 w-5 text-red-500"
                    width={16}
                    height={16}
                    fill="currentColor"
                    viewBox="0 0 16 16"
                    aria-hidden="true"
                >
                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8 4a.905.905 0 0 0-.9.995l.35 3.507a.552.552 0 0 0 1.1 0l.35-3.507A.905.905 0 0 0 8 4zm.002 6a1 1 0 1 0 0 2 1 1 0 0 0 0-2z" />
                </svg>
            </div>}
            {error && (
                <p
                    className="mt-2 text-xs text-red-600"
                    id="email-error"
                >
                    {error}
                </p>
            )}
        </div>
    );
};

export default InputField;