import type { LinksFunction, LoaderArgs, MetaFunction } from "@remix-run/node";
import { json } from "@remix-run/node";
import {
  Links,
  LiveReload,
  Meta,
  Outlet,
  Scripts,
  ScrollRestoration,
  useFetcher,
  useLoaderData,
  useLocation,
} from "@remix-run/react";

import tailwindStylesheetUrl from "./styles/tailwind.css";
import { getUser } from "~/utils/auth.server";
import { useEffect, useState } from "react";
import clsx from "clsx";
import { getThemeSession } from "./utils/theme.server";
import { gdprConsent } from "~/utils/cookie.server";
import { ThemeBody, ThemeHead, ThemeProvider } from "./utils/theme-provider";

import * as gtag from "~/utils/gtag.client";

export const links: LinksFunction = () => {
  return [{ rel: "stylesheet", href: tailwindStylesheetUrl }];
};

export const meta: MetaFunction = () => ({
  charset: "utf-8",
  title: "Assistant",
  viewport: "width=device-width,initial-scale=1",
  "color-scheme": "dark light",
});

export async function loader({ request }: LoaderArgs) {
  const cookieHeader = request.headers.get("Cookie");
  const cookie = (await gdprConsent.parse(cookieHeader)) || {};
  const themeSession = await getThemeSession(request);

  return json({
    theme: themeSession.getTheme(),
    user: await getUser(request),
    track: cookie.gdprConsent,
    gaTrackingId: process.env.GA_TRACKING_ID,
  });
}

export default function App() {
  const data = useLoaderData<typeof loader>();
  const location = useLocation();
  const analyticsFetcher = useFetcher();

  useEffect(() => {
    if (data.track && data.gaTrackingId?.length) {
      //TODO: check if first request is made (because script is still loading)
      gtag.pageview(location.pathname, data.gaTrackingId);
    }
  }, [location, data.gaTrackingId, data.track]);

  useEffect(() => {
    import("preline");
  }, []);

  return (
    <html lang="en">
      <ThemeProvider specifiedTheme={data.theme}>
        <head>
          <Meta />
          <Links />
          <ThemeHead ssrTheme={Boolean(data.theme)} />
        </head>
        <body
          className={clsx("h-screen dark:bg-slate-900", {
            dark: data.theme === "dark",
          })}
        >
          <Outlet />
          <ThemeBody ssrTheme={Boolean(data.theme)} />
          <ScrollRestoration />
          <Scripts />
          <LiveReload />
          <div
            id="cookies-simple-with-icon-and-dismiss-button"
            className={clsx(
              "fixed bottom-0 right-0 z-[60] mx-auto w-full p-6 sm:max-w-xl",
              {
                "pointer-events-none opacity-0": data.track,
                "pointer-events-auto opacity-100": !data.track,
              }
            )}
          >
            {/* Card */}
            <analyticsFetcher.Form
              method="post"
              action="/action/enable-analytics"
              className="rounded-xl border border-gray-200 bg-slate-100 p-4 shadow-sm dark:border-gray-700 dark:bg-gray-800"
            >
              <div className="grid gap-y-3 sm:flex sm:items-center sm:gap-y-0 sm:gap-x-5">
                <div className="sm:max-w-sm">
                  <h2 className="text-gray-500">
                    <span className="font-semibold text-gray-800 dark:text-gray-200">
                      We use cookies
                    </span>{" "}
                    to analyze our traffic and create a smooth user experience.
                  </h2>
                </div>
                <button
                  name="accept-gdpr"
                  value="true"
                  type="submit"
                  className="inline-flex items-center justify-center gap-2 rounded-md border border-transparent bg-blue-500 py-2 px-3 text-sm font-semibold text-white transition-all hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 dark:focus:ring-offset-gray-800"
                >
                  Accept
                </button>
              </div>
            </analyticsFetcher.Form>
            {/* End Card */}
          </div>
          {process.env.NODE_ENV === "development" ||
            !data.gaTrackingId ||
            !data.track ? null : (
            <>
              <script
                async
                src={`https://www.googletagmanager.com/gtag/js?id=${data.gaTrackingId}`}
              />
              <script
                async
                id="gtag-init"
                dangerouslySetInnerHTML={{
                  __html: `
                window.dataLayer = window.dataLayer || [];
                function gtag(){dataLayer.push(arguments);}
                gtag('js', new Date());
                gtag('config', '${data.gaTrackingId}', {
                  page_path: window.location.pathname,
                });
              `,
                }}
              />
            </>
          )}
        </body>
      </ThemeProvider>
    </html>
  );
}
