import {
  Link,
  useActionData,
  useLoaderData,
  useMatches,
} from "@remix-run/react";
import { AiOutlineClose } from "react-icons/ai";
import { BiTransfer } from "react-icons/bi";
import { ValidatedForm } from "remix-validated-form";
import InputField from "~/components/form/InputField";
import SelectField from "~/components/form/SelectField";
import SubmitButton from "~/components/form/SubmitButton";
import Modal from "~/components/Modal";
import { validator } from "~/routes/app/investments/new";
import { useMatchesData } from "~/utils";
import { INVESTMENT_CATEGORIES } from "~/utils/constants";

export default function ChangeInvestment({
  type = "edit",
}: {
  type?: "edit" | "new";
}) {
  const actionData = useActionData();
  const loaderData = useLoaderData();
  const match = useMatchesData("routes/app/investments");

  return (
    <Modal
      title={type === "edit" ? "Edit investment" : "New investment"}
      description={
        type === "edit"
          ? "You can edit the details of this investment here."
          : "You can create a new investment here."
      }
      onCloseUrl="/app/investments"
      icon={BiTransfer}
    >
      <ValidatedForm method="post" className="space-y-5" validator={validator}>
        <div className="grid grid-cols-2 gap-2">
          <InputField
            label="Symbol"
            name="symbol"
            defaultValue={loaderData?.investment?.symbol}
          />
          <SelectField
            defaultValue={loaderData?.investment?.category}
            label="Category"
            name="category"
            options={Object.keys(INVESTMENT_CATEGORIES).map((key) => {
              const label =
                INVESTMENT_CATEGORIES[
                  key as keyof typeof INVESTMENT_CATEGORIES
                ];
              return {
                label: `${label.logo} ${label.name}`,
                value: key,
              };
            })}
          />
        </div>
        <div className="grid grid-cols-2 gap-2">
          <InputField
            defaultValue={loaderData?.investment?.amount}
            label="Amount"
            name="amount"
            type="number"
          />
          <InputField
            defaultValue={loaderData?.investment?.unitPrice}
            label="Price per unit"
            name="unitPrice"
            type="number"
          />
        </div>
        <div className="grid grid-cols-2 gap-2">
          <SelectField
            label="Group"
            name="groupId"
            defaultValue={
              loaderData?.investment?.groupId
                ? loaderData?.investment?.groupId
                : "individual"
            }
            options={[
              {
                label: "Individual",
                value: "individual",
              },
              ...match.groups.map((group) => ({
                label: `Group: ${group.title}`,
                value: group.id,
              })),
            ]}
          />
        </div>
        <div className="grid grid-cols-2 gap-2">
          <InputField
            defaultValue={
              loaderData?.investment?.payDate
                ? new Date(loaderData?.investment?.payDate)
                    .toISOString()
                    .substr(0, 16)
                : new Date().toISOString().substr(0, 16)
            }
            label="Pay date"
            name="payDate"
            type="datetime-local"
          />
        </div>

        <div className="mt-5 flex justify-end gap-x-2">
          <Link
            to="/app/investments"
            className="inline-flex items-center justify-center gap-2 rounded-md border bg-slate-100 py-2 px-3 align-middle text-sm font-medium text-slate-700 shadow-sm transition-all hover:bg-slate-50 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-offset-2 focus:ring-offset-white dark:border-slate-700 dark:bg-slate-800 dark:text-slate-400 dark:hover:bg-slate-800 dark:hover:text-white dark:focus:ring-offset-slate-800"
          >
            <AiOutlineClose className="h-4 w-4" />
            Close
          </Link>
          {type === "edit" && (
            <button
              type="submit"
              className="inline-flex items-center justify-center gap-2 rounded-md border border-transparent bg-red-500 py-3 px-4 text-sm font-semibold text-white transition-all hover:bg-red-600 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-offset-2 dark:focus:ring-offset-gray-800"
              name="action"
              value="delete"
            >
              Delete
            </button>
          )}
          <SubmitButton />
        </div>
        {actionData?.error && (
          <div
            className="rounded-md border border-red-200 bg-red-50 p-4 text-sm text-red-600"
            role="alert"
          >
            <span className="font-bold">Error</span> {actionData?.error}
          </div>
        )}
      </ValidatedForm>
    </Modal>
  );
}
