import type { Expense } from "@prisma/client";
import { redirect } from "@remix-run/server-runtime";

import { prisma } from "~/db.server";
import { authenticator } from "~/utils/auth.server";
import { getUserGroups } from "./user.server";

export const getExpenses = async (request: Request) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");

  const userGroups = await getUserGroups(user.id);
  const expenses = await prisma.expense.findMany({
    where: {
      OR: [
        {
          groupId: {
            in: userGroups.map((group) => group.id),
          },
        },
        {
          userId: user.id,
        },
      ],
    },
  });

  return expenses;
};

export const createExpense = async (request: Request, data: Expense) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");

  const { groupId, ...otherData } = data;
  if (data.groupId === "individual") {
    const expense = await prisma.expense.create({
      data: {
        ...otherData,
        user: {
          connect: {
            id: user.id,
          },
        },
      },
    });
    return expense;
  } else {
    const expense = await prisma.expense.create({
      data: {
        ...otherData,
        Group: {
          connect: {
            id: data.groupId,
          },
        },
      },
    });
    return expense;
  }
};

export const updateExpense = async (request: Request, data: Expense) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");

  const { groupId, ...otherData } = data;
  if (data.groupId === "individual") {
    const expense = await prisma.expense.update({
      where: {
        id: data.id,
      },
      data: {
        ...otherData,
        user: {
          connect: {
            id: user.id,
          },
        },
        Group: {
          disconnect: true,
        },
      },
    });

    return expense;
  } else {
    const expense = await prisma.expense.update({
      where: {
        id: data.id,
      },
      data: {
        ...otherData,
        Group: {
          connect: {
            id: data.groupId,
          },
        },
      },
    });

    return expense;
  }
};

export const getExpense = async (request: Request, id: string) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");

  const expense = await prisma.expense.findUnique({
    where: {
      id,
    },
  });

  return expense;
};

export const deleteExpense = async (request: Request, id: string) => {
  const user = await authenticator.isAuthenticated(request);
  if (!user) throw redirect("/login");

  const expense = await prisma.expense.delete({
    where: {
      id,
    },
  });

  return expense;
};
